                                <span class="date notChoice" v-else>{{scope.row[item].date}}</span>

                                <!--值班列表-->
                                <ul class="dayList clearfix">
                                    <li v-for="(itemK, index) in scope.row[item].data" :key="itemK.id" class="clearfix" v-if="index < scope.row[item].showNum">
