                                    <i class="el-icon-caret-bottom iconCart" @click="caretBottom(scope.row[item], scope.row)" v-if="scope.row[item].data.length> showNum && scope.row[item].showNum == showNum"></i>
                                    <i class="el-icon-caret-top iconCart" @click="caretTop(scope.row[item], scope.row)" v-if="scope.row[item].data.length> showNum && scope.row[item].showNum > showNum"></i>
