<script>
    // @ is an alias to /src
    import Dialog from '@/components/dialog/index.vue'
    import SearchBtn from '@/components/search/index.vue'

    export default {
        name: '',
        components: { Dialog, SearchBtn },
        data() {
            return {
                form: {
                    name: '',
                    department: '',
                    time: ''
                },
                // 隐藏的个数
                showNum: 3,
                // 校验
                rules: {
                    name: [
                        {required: false, message: '请输入内容', trigger: 'blur'}
                    ],
                    department: [
                        {required: false, message: '请输入内容', trigger: 'blur'}
                    ]
                },
                searchLoading: true,
                // 搜索结果
                searchData: {
                    '2019-05-16': [
                        {
                            id: '1222',
                            time: '08:00-11:00',
                            name: '张晓丽',
                            phone: '15789067789',
                            notice: '10分钟前',
                            department: '行政部'
                        },
                        {
                            id: '1333',
                            time: '13:00-18:00',
                            name: '李晓',
                            phone: '15789067779',
                            notice: '无',
                            department: '行政部'
                        },
                        {
                            id: '144',
                            time: '19:00-21:00',
                            name: '卫佳佳',
                            phone: '15789057779',
                            notice: '无',
                            department: '行政部'
                        },
                        {
                            id: '154',
                            time: '19:00-21:00',
                            name: '蒋佳佳',
                            phone: '15789057779',
                            notice: '无',
                            department: '行政部'
                        },
                        {
                            id: '156',
                            time: '19:00-21:00',
                            name: '李薇薇',
                            phone: '15789057779',
                            notice: '无',
                            department: '行政部'
                        }
                    ],
                    '2019-05-17': [
                        {
                            id: '1522',
                            time: '08:00-11:00',
                            name: '张晓丽',
                            phone: '15789067789',
                            notice: '10分钟前',
                            department: '行政部'
                        }
                    ]
                },
                // 日历表数据
                calendarData: [],
                // 年月
                showTime: '',
                header: ['周日', '周一', '周二', '周三', '周四', '周五', '周六'],
                // 当前编辑|新增的值班
                editForm: {

                }
            }
        },
        mounted: function() {
            const day = this.parseTime(new Date(), '{y}-{m}');
            this.form.time = day;
            this.showTime = day.split('-')[0] + '年' + day.split('-')[1] + '月';
            // 设置日历表
            this.setCalendarData();
        },
        methods: {
            // 展开
            caretBottom(data, row) {
                let calendarData = this.calendarData;
                let index = this.calendarData.indexOf(row);
                data.showNum = data.data.length;
                this.$set(calendarData, index, row);
            },
            // 缩回
            caretTop(data, row) {
                let calendarData = this.calendarData;
                let index = this.calendarData.indexOf(row);
                data.showNum = this.showNum;
                this.$set(calendarData, index, row);
            },
            onSearch(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        // 校验通过
                        console.log('执行查询.');
                        // 查询结束成功后，执行  this.setCalendarData()
                    } else {
                        return false;
                    }
                });
            },
            // 编辑
            editor(item) {
                console.log(item);
            },
            // 删除
            del(item, data) {
                const index = data.indexOf(item);
                data.splice(index, 1);
            },
            // 新增值班
            onAdd(item, event) {
                if (item.hasChoice) {
                   
                }
            },
            // 编辑
            onEdit(item) {
            },
            // 月份切换 type: left | right
            onTimeRange(type) {
                if (type === 'left') {
                    this.form.time = this.getPreMonth(this.form.time);
                } else if (type === 'right') {
                    this.form.time = this.getNextMonth(this.form.time);
                }
                this.showTime = this.form.time.split('-')[0] + '年' + this.form.time.split('-')[1] + '月';
                this.setCalendarData();
            },
            onTimeChange(value) {
                this.form.time = value;
                this.setCalendarData();
            },
            // 切换到今日
            onSwitchToDay() {},
            // 时间格式
            parseTime(time, cFormat) {
                if (arguments.length === 0) {
                    return null
                }
                const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
                let date
                if (typeof time === 'object') {
                    date = time
                } else {
                    if (('' + time).length === 10) time = parseInt(time) * 1000
                    date = new Date(time)
                }
                const formatObj = {
                    y: date.getFullYear(),
                    m: date.getMonth() + 1,
                    d: date.getDate(),
                    h: date.getHours(),
                    i: date.getMinutes(),
                    s: date.getSeconds(),
                    a: date.getDay()
                };
                const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
                    let value = formatObj[key]
                    // Note: getDay() returns 0 on Sunday
                    if (key === 'a') { return ['周日', '周一', '周二', '周三', '周四', '周五', '周六'][value ] }
                    if (result.length > 0 && value < 10) {
                      value = '0' + value;
                    }
                    return value || 0;
                })
                return time_str;
            },
            // 获取指定月份的天数
            getDate(year, month) {
                // 由于JavaScript中day的范围为1~31中的值，所以当设为0时，会向前一天，也即表示上个月的最后一天
                const lastDay = new Date(year, month, 0);
                return lastDay.getDate();
            },
            // 设置日历表
            setCalendarData() {
                this.calendarData = [];
                const lastDay = this.getDate(this.form.time.split('-')[0], this.form.time.split('-')[1]);
                // 今日
                const toDayTime = new Date(this.parseTime(new Date(), '{y}-{m}-{d}')).getTime();
                // 遇到day是周日的话，就开启一行
                // [{'周日': {},'周一': {},..}]
                const obj = {
                    '周日': {
                        date: '',
                        day: '周日',
                        data: [],
                        // 表示这天是否可选
                        hasChoice: false
                    },
                    '周一': {
                        date: '',
                        day: '周一',
                        data: [],
                        // 表示这天是否可选
                        hasChoice: false
                    },
                    '周二': {
                        date: '',
                        day: '周二',
                        data: [],
                        // 表示这天是否可选
                        hasChoice: false
                    },
                    '周三': {
                        date: '',
                        day: '周三',
                        data: [],
                        // 表示这天是否可选
                        hasChoice: false
                    },
                    '周四': {
                        date: '',
                        day: '周四',
                        data: [],
                        // 表示这天是否可选
                        hasChoice: false
                    },
                    '周五': {
                        date: '',
                        day: '周五',
                        data: [],
                        // 表示这天是否可选
                        hasChoice: false
                    },
                    '周六': {
                        date: '',
                        day: '周六',
                        data: [],
                        // 表示这天是否可选
                        hasChoice: false
                    }
                };
                let nowSetIndex = 0;
                for (let i = 1; i <= lastDay; i++) {
                    let str = i;
                    if (i< 10) {
                        str = '0' + i;
                    }
                    const nowDate = new Date(this.form.time + '-' + str);
                    const day = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'][nowDate.getDay()];
                    if (day === '周日') {
                        this.calendarData.push(JSON.parse(JSON.stringify(obj)));
                        nowSetIndex = this.calendarData.length - 1;
                    } else {
                        if (!this.calendarData.length) {
                            this.calendarData.push(JSON.parse(JSON.stringify(obj)));
                            nowSetIndex = 0;
                        }
                    }
                    const nowObj = this.calendarData[nowSetIndex];
                    nowObj[day].date = i;
                    // 判断是否可设置
                    if (nowDate.getTime() >= toDayTime) {
                        nowObj[day].hasChoice = true;
                    }
                    // 判断是否今日
                    if (nowDate.getTime() == toDayTime) {
                        nowObj[day].today = true;
                    }
                    // 设置该日期的值班信息
                    nowObj[day].data = this.searchData[this.form.time + '-' + str] || [];
                    nowObj[day].showNum = this.showNum; 
                }
                console.log(JSON.stringify(this.calendarData));
            },
            // 获取上一个月 2019-01   ==> 2018-12
            getPreMonth(date) {
                const arr = date.split('-');
                const year = arr[0]; //获取当前日期的年份
                const month = arr[1]; //获取当前日期的月份
                let year2 = year;
                let month2 = parseInt(month) - 1;
                if (month2 == 0) {
                    year2 = parseInt(year2) - 1;
                    month2 = 12;
                }
                if (month2 < 10) {
                    month2 = '0' + month2;
                }
                return year2 + '-' + month2;

            },
            // 获取下一个月 2019-01   ==> 2019-02
            getNextMonth(date) {
                const arr = date.split('-');
                const year = arr[0]; //获取当前日期的年份
                const month = arr[1]; //获取当前日期的月份
                let year2 = year;
                let month2 = parseInt(month) + 1;
                if (month2 == 13) {
                    year2 = parseInt(year2) + 1;
                    month2 = 1;
                }
                if (month2 < 10) {
                    month2 = '0' + month2;
                }
            
                return year2 + '-' + month2;
            }
        }
    }
</script>

